@media(min-width: 1904px){.wallet-content-container{max-width:1603px !important}}
html{min-width:320px !important;font-size:14px !important}
.container{max-width:1185px !important}
.monospace{font-family:monospace !important}
i{font-family:"Material Icons" !important}
div{padding:0;margin:0;font-size:14px;font-weight:400}
h4{font-weight:400}
.color--inherit{color:inherit !important}
.text-transform--initial{text-transform:initial !important}
.text-transform--none{text-transform:none !important}
.text-decoration--none{text-decoration:none !important}
.text-decoration--underline{text-decoration:underline}
.word-break--break-all{word-break:break-all;white-space:initial}
.break-word{word-break:break-word;white-space:initial}
.white-space--nowrap{white-space:nowrap !important}
.letter-spacing--none{letter-spacing:0 !important}
.letter-spacing--initial{letter-spacing:initial !important}
.line-height--initial{line-height:initial !important}
.pointer-event--none{pointer-events:none}
.user-select--none{-webkit-user-select:none;-moz-user-select:none;user-select:none}
.cursor--pointer{cursor:pointer !important}
.border-radius--12px{border-radius:12px !important}
.border-radius--10px{border-radius:10px !important}
.border-radius--8px{border-radius:8px !important}
.border-radius--5px{border-radius:5px !important}
.bs-wallet-block{box-shadow:0 12px 17px #151d3f08 !important}
.bs-small{box-shadow:0px 1.6px 6px #00000045 !important}
.overflow--hidden{overflow:hidden !important}
.position--relative{position:relative}
.width--full{width:100% !important}
.height--full{height:100% !important}
.display--block{display:block !important}
.spacer-y-medium{height:5rem}
.rotate.transition{transition:all 1200ms ease}
.rotate.r180{transform:rotate(180deg)}
.rotate.r90{transform:rotate(90deg)}
.make-white-svg{filter:brightness(10)}
input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}
input[type=number]{-moz-appearance:textfield}
*{scrollbar-width:auto;scrollbar-color:#c8c8c8 #f4f4f4}
*::-webkit-scrollbar{width:10px}
*::-webkit-scrollbar-track{background:#f4f4f4}
*::-webkit-scrollbar-thumb{background-color:#c8c8c8;border-radius:10px;border:2px solid #f4f4f4}
.v-dialog{border-radius:12px !important}
