.mew-address{color:inherit;font-family:"PT Mono"}
.mew-title{font-size:3.428rem;font-weight:700;line-height:4.571rem;letter-spacing:.0178rem}
.mew-subtitle,h1{font-size:2.285rem;font-weight:700;line-height:3.142rem;letter-spacing:.0178rem}
.mew-heading-1{font-size:1.714rem;font-weight:700}
.mew-heading-2,h2{font-size:1.429rem !important;font-weight:700 !important}
.mew-heading-3,h3{font-size:1.143rem;font-weight:700}
.mew-body,p,h5{font-size:1rem !important;font-weight:400}
.mew-caption{font-size:.8571rem;font-weight:700;text-transform:uppercase}
.capitalize{text-transform:capitalize !important}
.no-decoration{text-decoration:none !important}
.mew-header-block{color:var(--v-titlePrimary-base);font-weight:bold;margin-bottom:15px;padding-left:10px}
.mew-label,h6{font-size:.8571rem;font-weight:400}
.mew-heading-4,h4{font-size:1.143rem;font-weight:400}
a{text-decoration:none}
.bg-white{background-color:var(--v-white-base) !important}
.break-word{white-space:normal;word-wrap:break-word}
.truncate{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}
.no-display{display:none}
.no-pointer-events{pointer-events:none}
.all-pointer-events{pointer-events:all}
.text-decoration-underline{text-decoration:underline}
.cursor-pointer{cursor:pointer}
.full-width{width:100%}
.full-max-width{max-width:100%}
.full-height{height:100%}
.column-reverse{flex-direction:column-reverse}
.tooltip-inner{background-color:var(--v-white-base) !important;border:1px solid var(--v-inputBorder-base) !important;opacity:1 !important}
.tooltip-inner:after{content:"";position:absolute;top:100%;left:0;right:0;margin:0 auto;width:1px;z-index:15;border-left:10px solid transparent;border-right:10px solid transparent;border-top:10px solid var(--v-inputBorder-base)}
.tooltip-inner:before{content:"";position:absolute;top:100%;left:0;right:0;margin:0 auto;width:1px;z-index:20;border-left:8px solid transparent;border-right:8px solid transparent;border-top:8px solid var(--v-white-base)}
.v-application .v-select-list{padding:0}
.v-application .v-autocomplete__content.v-menu__content{border-bottom:1px solid var(--v-dropdownBorder-base);border-left:1px solid var(--v-dropdownBorder-base);border-radius:4px;border-right:1px solid var(--v-dropdownBorder-base);box-shadow:0px 5px 5px -3px rgba(13,41,66,.2),0px 8px 10px 1px rgba(13,41,66,.14),0px 3px 14px 2px rgba(13,41,66,.12)}
.v-application .simple-input.v-text-field>.v-input__control>.v-input__slot:before{border-style:none}
.v-application .simple-input.v-text-field>.v-input__control>.v-input__slot:after{border-style:none}
.v-application .theme--light.v-text-field--outlined:not(.v-input--is-focused).v-input--is-disabled>.v-input__control>.v-input__slot fieldset,.v-application .theme--dark.v-text-field--outlined:not(.v-input--is-focused).v-input--is-disabled>.v-input__control>.v-input__slot fieldset{color:var(--v-inputDisabled-base) !important}
.v-application .v-text-field--outlined:not(.v-input--is-focused):not(.v-input--has-state)>.v-input__control>.v-input__slot fieldset{color:var(--v-inputBorder-base) !important}
.v-application .v-text-field--outlined:not(.v-input--is-focused):not(.v-input--has-state)>.v-input__control>.v-input__slot:hover>fieldset{border:1px solid var(--v-greyPrimary-base) !important}
.v-application .theme--light.address-select.v-input,.v-application .theme--light.mew-input.v-input,.v-application .theme--light.mew-text-area.v-input,.v-application .theme--light.mew-select.v-input{color:transparent}
.v-application .v-menu__content{border:1px solid var(--v-dropdownBorder-base);box-shadow:0px 5px 5px -3px rgba(13,41,66,.2),0px 8px 10px 1px rgba(13,41,66,.14),0px 3px 14px 2px rgba(13,41,66,.12)}
.v-application .v-menu__content .v-select-list .v-subheader{align-items:center;background-color:var(--v-selectHeaderBg-base);border:1px solid var(--v-selectBorder-base);color:var(--v-textSecondary-base);display:flex;height:32px;padding:8px 22px 8px 48px}
.v-application .v-menu__content .v-select-list .v-list-item{border-bottom:1px solid var(--v-selectBorder-base)}
.v-application .v-input input{color:var(--v-textDark-base) !important;font-size:14px}
.v-application .v-input.v-input--is-disabled input{color:var(--v-inputDisabled-base) !important}
.v-application .v-input .v-label{color:var(--v-textMedium-base);font-weight:400;font-size:1rem}
.v-application .v-input .v-label.v-label--is-disabled{color:var(--v-inputDisabled-base)}
.v-application .v-input input::placeholder{color:var(--v-textLight-base);font-weight:400}
.v-application .v-input .v-text-field__suffix{color:var(--v-searchText-base) !important}
.v-application .v-input .disable-icon{filter:grayscale(100%);pointer-events:none}
.v-application .blockie-placeholder{height:25px;width:25px;border-radius:50%}
.v-application .v-dialog{border-radius:12px !important}
.v-application .v-btn.mew-button{text-transform:capitalize}
.v-application .v-btn.mew-button .v-btn__content{max-width:100%}
html,body{font-family:"Roboto",sans-serif;font-size:14px;font-style:normal;font-weight:400}
input::placeholder{font-family:"Roboto",sans-serif;font-style:normal;font-weight:400}
.mew-address{font-family:"PT Mono"}